.navbar {
  width: 100%;
  background: var(--primary);
  box-shadow: 3px 0 15px 0 rgba(0, 0, 0, 0.2);
  position: relative;
  color: white;
  padding: 1em;
  display: flex;
  justify-content: space-between;
  align-items: center;

  &__title {
    text-align: center;

    &:hover {
      cursor: pointer
    }
  }
}